	module.exports = {"data":{"talkJson":{"description":"The latest version of Polymer is fast and lean. Learn how teams at Google have successfully launched sites using Polymer and the latest platform APIs: Web Animations, Service Workers for offline and push notifications, and material design. And if you're new to Polymer we'll show you how to get started building mobile-first apps.","meta":{"duration":"PT46M28S"},"presenter":{"name":"Eric Bidelman, Matthew McNulty, Taylor Savage"},"slug":"polymer-and-modern-web-apis-in-production-at-google-scale","source":{"id":"fD2As5RmM8Q","provider":"youtube"},"tag":["Google I/O 2015","Polymer","Web components"],"title":"Polymer and modern web APIs: In production at Google scale"},"allTalkJson":{"edges":[{"node":{"description":"Speaker(s):\nRob Dodson\n\nDescription:\nEntering the multi-screen era means rethinking how we build our applications. Producing a few PSDs doesn't cut it anymore, we have to start seeing the things we design as components within larger systems. Join us to learn how to use Polymer to revolutionize your design process. With these new tools we can create the UIs of the future, and shorten the time between concept and reality.\n\nWatch all Google I/O 2014 videos at: g.co/io14videos","meta":{"duration":"PT41M31S"},"presenter":{"name":null},"source":{"id":"HKrYfrAzqFA","provider":"youtube"},"slug":"google-io-2014-unlock-the-next-era-of-ui-development-with-polymer","tag":["Google","I/O","IO","io14","Develop","Polymer","product: web","fullname: Rob Dodson","Location: other","Team: Scalable Advocacy","Type: Live Event","GDS: Upload only","Other: NoGreenScreen"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/HKrYfrAzqFA/default.jpg","height":90,"width":120}},"title":"Google I/O 2014 - Unlock the next era of UI development with Polymer"}}]}},"pathContext":{"slug":"polymer-and-modern-web-apis-in-production-at-google-scale","tags":["Google I/O 2015","Polymer","Web components"]}}

